                    <div v-if="!item.type">
                        <el-input v-if="item.module === 'name'" v-model="name" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'contactPerson'" v-model="contactPerson" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'mobile'" v-model="mobile" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'memo'" v-model="memo" :placeholder="item.placeholder"/>
                    </div>
                    <!-- select -->
                    <div v-if="item.type === 'select'">
                        <el-select v-if="item.type === 'select'" v-model="item.model" placeholder="全部">
